<script>
import loading from "@/tkViews/components/layout/Loading.vue";
import DropzoneFileUpload from "@/tkViews/components/Inputs/DropzoneFileUpload.vue";
import avatar from "@/tkViews/components/layout/Avatar";
import { Select, Option } from "element-ui";
import commons from "@/util/commons";
import userService from "@/services/userService";
import collections from "@/services/collections";
import tkSwitch from '@/tkViews/components/Inputs/TKSwitch'
export default {
  components: {
    loading,
    DropzoneFileUpload,
    avatar,
    [Select.name]: Select,
    [Option.name]: Option,
    tkSwitch
  },
  props: {
    title: String,
    left: {
      type: String,
      default: "",
    },
    center: {
      type: String,
      default: "",
    },
    right: {
      type: String,
      default: "",
    },
    leftType: {
      type: String,
      default: "secondary",
    },
    centerType: {
      type: String,
      default: "secondary",
    },
    rightType: {
      type: String,
      default: "main",
    },
  },
  data() {
    return {
      show: false,
      showUpload: false,

      isInvalid: false,
      countryCodeSelected: "+1",
      countrySelects: this.$tools.countrySelects,
      fileSingle: [],
      userId: "",
      studioId: "",
      isProfileDataLoading: true,
      model: {
        user: {
          userId: "",
          name: "",
          phone: "",
          email: "",
          website: "",
        },
        studio: {
          id: "",
          memberLevelId: 1,
          name: "",
          storefrontColor: "",
        },
        teacherPermissions: {
          userId: "",
          studioProfile: false,
          admin: false,
          teacherProfile: true,
          scheduling: false,
          billing: false,
          insights: false,
        },
      },
    };
  },
  watch: {
    fileSingle(newVal, oldVal) {
      setTimeout(() => {
        console.log('----', newVal[0]);
        console.log('----', newVal[0].dataURL);
        this.avatarUrl = newVal[0].dataURL;
      }, 200);
    },
    countryCodeSelected(val, old) {
      let self = this;
      document.getElementsByClassName(
        "tk-select-border-none"
      )[0].childNodes[1].childNodes[1].style.display = "none";
      self.countrySelects.options.some((item) => {
        if (item.value === val) {
          setTimeout(() => {
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].value = item.areaCode;
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].style.display = "block";
          }, 10);
        }
      });
    },
  },
  async mounted() {
    let self = this;
    self.init();

    console.log("-------");
    let studioInfo = await userService.studioInfo();
    self.model.studio = studioInfo;
    console.log("获取到的Studio信息: ", studioInfo);
    let userId = commons.userId();
    collections.user.doc(userId).onSnapshot((doc) => {
      console.log("监听的数据: ", doc.data());
      if (doc.exists) {
        self.model.user = doc.data();
      }
    });
    let teacherPermissions = await userService.teacherPermissions.get();
    self.model.teacherPermissions = teacherPermissions;
    self.isProfileDataLoading = false;
  },
  methods: {
    close() {
      this.$emit("close");
      this.modalClose();
    },
    onLeftTapped() {
      this.$emit("onLeftTapped");
      this.modalClose();
    },
    onCenterTapped() {
      this.$emit("onCenterTapped");
      this.modalClose();
    },
    onRightTapped() {
      this.$emit("onRightTapped");
      this.modalClose();
    },
    modalClose() {
      document.getElementsByTagName("body")[0].classList.remove("modal-open");
    },
    init() {
      let self = this;
      setTimeout(() => {
        self.show = true;
      }, 200);
    },
    async onStudioProfileChanged() {
      console.log("点击studioProfile");
      await this.updateTeacherPromissions();
    },
    async onAdminChanged() {
      console.log("点击Admin");
      await this.updateTeacherPromissions();
    },
    async onTeacherProfileChanged() {
      console.log("点击TeacherProfile");
      await this.updateTeacherPromissions();
    },
    async onSchedulingChanged() {
      console.log("点击Scheduling");
      await this.updateTeacherPromissions();
    },
    async onBillingChanged() {
      console.log("点击Billing");
      await this.updateTeacherPromissions();
    },
    async onInsightsChanged() {
      console.log("点击Insights");
      await this.updateTeacherPromissions();
    },
    async updateTeacherPromissions() {
      let self = this;
      return new Promise(async (resolve, reject) => {
        try {
          let data = self.model.teacherPermissions;
          await userService.teacherPermissions.update(data);
          resolve();
        } catch (error) {
          reject(error);
        }
      });
    },
    change(data) {
      console.log(data);
    },
    validationEmail() {
      let self = this;

      if (self.$tools.isNull(self.model.user.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.model.user.email);
      }
    },
  },
};
</script>

